@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}

body {
  background-color: #f5f6fa;
}

.app {
  display: flex;
  justify-content: space-evenly;
  background-color: #f5f6fa;
  padding: 20px;
}

.app__dropdown {
  background-color: white;
}

.app__header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

.app__header > h1 {
  color: #fc3c3c;
  font-size: 2rem;
}

.app__stats {
  display: flex;
  justify-content: space-between;
}

.app__left {
  flex: 0.9 1;
}

.app__information > h3 {
  color: #6a5d5d;
  font-weight: 400;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.app__information > h3:last-of-type {
  margin-top: 1rem;
}

@media (max-width: 990px) {
  .app {
    flex-direction: column;
  }
}

.infoBox {
  flex: 1 1;
}

.infoBox:not(:last-child) {
  margin-right: 10px;
}

.infoBox--selected {
  border-top: 10px solid greenyellow;
}

.infoBox--red {
  border-color: red;
}

.infoBox__cases--green {
  color: lightgreen !important;
}

.infoBox__cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
}

.infoBox__total {
  color: #6c757d;
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}

.table {
  margin-top: 20px;
  overflow: scroll;
  color: #6a5d5d;
  background-color: white;
  height: 400px;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.5rem;
  border: none;
}

.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}

.map {
  height: 500px;
  background-color: white;
  padding: 1rem;
  border-radius: 20px;
  margin-top: 16px;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
  height: 100%;
  border-radius: 12px;
}

.info-flag img {
  width: 100px;
  border-radius: 5px;
}

.info-name {
  font-size: 20px;
  font-weight: bold;
  color: #555;
}

.info-container {
  width: 150px;
}

.info-flag {
  height: 80px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
}

.info-confirmed,
.info-recovered,
.info-deaths {
  font-size: 16px;
  margin-top: 5px;
}

body {
  font-family: 'Poppins';
  margin: 0;
  width: 100%;
  background-image: url("https://i.pinimg.com/736x/f4/ac/6a/f4ac6aac3afb587f6cae04155656fca3.jpg");
}

.up, .pp {
  font-size: larger;
  font-weight: 350;
  width: 40vw;
}

.uh2 {
  font-size: xx-large;
}
.cuntainer {
  width: auto;
  height: 100vh;
}

.covid-pic {
  position: absolute;
  z-index: -1;
  right: 0;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
  border-radius: 2%;
}

.inner-cuntainer {
  display: flex;
  height: 80.06%;
  width: auto;
  align-items: center;
  padding-right: 5%;
  padding-left: 5%;
  background-image: linear-gradient(to right, white 40%, rgba(255, 255, 255, 0) 75%);
  border-radius: 2%;
}

.inner-cuntainer.second {
  background-image: linear-gradient(to right, white 40%, rgba(255, 255, 255, .5));
}

.inner-cuntainer.third {
  background-color: white;
}

.pp {
  width: 80vw !important;
}

.vitb-pic {
  position: absolute;
  z-index: -1;
  right: 0;
  height: 80vh;
  border-radius: 2%;
}

.pic-container {
  display: flex;
  flex-direction: column;
}

.row1 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.row1.center {
  justify-content: center;
}

.prof-pic {
  border-radius: 50%;
  width: 15%;
  margin: 2% 15% 2% 20%;
  transition: all .2s ease-in-out;
}

.prof-pic:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
